html {
  font-size: 62.5%;
}

body {
  font-size: 1.6rem;
}

body {
  font-family: "Mulish", sans-serif;
  font-weight: 400;
  color: rgb(61, 28, 17);
}
body.text-content {
  font-family: "Mulish", sans-serif;
  font-weight: 400;
  color: rgb(61, 28, 17);
}

.headline, h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Cormorant", serif;
  font-weight: 700;
  color: rgb(61, 28, 17);
}

h1.headline, h1 {
  font-size: 4.8rem;
}

h2.headline,
h2 {
  font-size: 4.2rem;
}

h3.headline,
h3 {
  font-size: 3.6rem;
}

h4.headline,
h4 {
  font-size: 3rem;
}

section {
  padding: 65px 0;
}

.bg-dark {
  background-color: rgb(61, 28, 17);
}
.bg-dark .headline {
  color: white;
}
.bg-dark .subheadline {
  color: white;
}
.bg-dark .text-content {
  color: white;
}
.bg-dark * {
  color: white;
}

.bg-light {
  background-color: rgb(210, 189, 182);
}
.bg-light .headline {
  color: unset;
}
.bg-light .subheadline {
  color: unset;
}
.bg-light .text-content {
  color: unset;
}

.problem {
  padding: 0;
}
.problem__problem {
  padding: 65px 0;
}
.problem__success {
  padding: 65px 0;
}

.testimonials {
  padding: 65px 0;
}

.instagram {
  padding: 65px 0;
}

.get-started {
  padding: 65px 0;
}

.btn, .btn__primary--fill, .header .navbar__menu .menu-item.cta {
  border: 2px solid transparent;
  border-radius: 1rem;
  padding: 1rem 1.875rem;
  display: block;
  transform: translateY(0);
  transition: background-color 0.3s ease, border-color 0.3s ease, transform 0.3s ease, box-shadow 0.3s ease, color 0.3s ease;
}
.btn:hover, .btn__primary--fill:hover, .header .navbar__menu .menu-item.cta:hover {
  box-shadow: 2px 4px 8px rgba(0, 0, 0, 0.35);
  transform: translateY(-4px);
}
.btn__primary--fill, .header .navbar__menu .menu-item.cta {
  background-color: rgb(61, 28, 17);
  color: white;
}
.btn__primary--fill a, .header .navbar__menu .menu-item.cta a {
  color: white;
}
.btn__primary--fill:hover, .header .navbar__menu .menu-item.cta:hover, .btn__primary--fill:active, .header .navbar__menu .menu-item.cta:active, .btn__primary--fill:focus, .header .navbar__menu .menu-item.cta:focus {
  background-color: transparent;
  border-color: rgb(61, 28, 17);
  color: rgb(61, 28, 17);
}
.btn__primary--fill:hover a, .header .navbar__menu .menu-item.cta:hover a, .btn__primary--fill:active a, .header .navbar__menu .menu-item.cta:active a, .btn__primary--fill:focus a, .header .navbar__menu .menu-item.cta:focus a {
  color: rgb(61, 28, 17);
}

a.btn, a.btn__primary--fill, .header .navbar__menu a.menu-item.cta {
  text-decoration: none;
}

.header {
  height: fit-content;
}
.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__logo {
  display: inline-block;
}
.header__logo .logo-image {
  padding: 1.5rem;
}
.header__logo .logo-image img {
  width: 18rem;
  height: auto;
  object-fit: contain;
}
.header .navbar {
  display: inline-block;
}
.header .navbar a {
  transition: color 0.3s ease;
  text-decoration: none;
}
.header .navbar a:not(:has(h1)) {
  text-transform: uppercase;
}
.header .navbar__menu {
  list-style: none;
  padding: unset;
  display: flex;
}
.header .navbar__menu .menu-item {
  padding: 10px;
  margin: 0 10px;
  display: inline-block;
  border-bottom: 1px solid transparent;
  transition: border-color 0.3s ease;
}
.header .navbar__menu .menu-item a {
  color: rgb(61, 28, 17);
  font-weight: 700;
  font-size: 2.4rem;
  font-family: "Cormorant", serif;
  text-transform: capitalize;
  letter-spacing: 0.1rem;
}
.header .navbar__menu .menu-item:hover {
  border-color: rgb(61, 28, 17);
  cursor: pointer;
}
.footer {
  padding: 65px 0;
}
.footer__menu {
  display: flex;
  flex-direction: row;
  list-style: none;
}
.footer__menu a {
  text-decoration: none;
  margin: 0 1rem;
}
.footer .logo {
  color: white;
}